<script>
  import htp from "../../api/http";
  import urls from '../../api/apiurl';

  let timmer = null;

  export default {
    data() {
      return {
        constOption: {
          defaultSourceWidth: 800, //窗口大小
          defaultSourceHeight: 600, //窗口大小
          minSourceLength: 36 //像素大小
        },
        eventNames: [],
        mapPicture: ['dvi', 'hdmi', 'vga', 'sdi', 'cvbs', 'dualdvi', 'hdmi1', 'dp1', 'dp2', 'hdmi2', 'usb', 'HDbaseT', 'osd', 'osdtext'],// 图片映射
        colors: ['#c37dc1', '#777ed2', '#a777d8', '#cc9898', '#38a39a', '#599dd2', '#caa463', '#41434b', '#c16e6e'],
        colorIndex: 0,
        inputSources: [], //数据源
        winList: [],//窗口
        osdList: [],
        lay: {
          rows: 1,
          cols: 1
        },
        zIndexMenuOption: {
          isLock: false, //锁定图标
          zIndex: 1, //打开的源的层级顺序
          activeSource: 0, //当前激活的源
          swiperActiveIndex: -1, //拖拽新增的源下标
          isShowTake: false, //显示take按钮
          operateBtnGroupIndex: -1, //源长按显示按钮组
          langPressX: 0, //长按显示按钮的位置
          langPressY: 0, //长按显示按钮的位置
          panStatus: 0 //左右滑动还是上下滑动
        },
        initSource: {//初始化source
          ox: 0, //上一次的x坐标
          oy: 0, //上一次的y坐标
          ow: 0, //上一次的宽度
          oh: 0, //上一次的高度
          x: 0, //x坐标
          y: 0, //y坐标
          w: 0, //宽度
          h: 0, //高度
          show: false, //是否显示
          name: '', //源名称
          isAlignRight: false, //是否开启右侧对齐(左下角按钮操作时需开启右侧对齐)
          isLock: false, //锁定源后不能移动和层级操作
          winId: 0,//窗口Id
          id: 0,//源Id
          type: '',//类型
          zIndex: 10000, //Z序
          status: 0,
          replaceStatus: false, //替换源标识
          count: 0,
          sourceType: '',
          colorIndex: 0,
          fullSourceStatus: false //源或osd是否已经满了
        },
        enteringSource: {},//移入中的源
        sourceWindow: {},//源窗口
        openedSources: [], //打开的源
        layer: {
          showWidth: '',
          showHeight: '',
          width: '',
          height: '',
          x: '',
          y: '',
          winId: 0,
          type: '',
          id: 0,
          zIndex: 0
        },
        windowId: '',//窗口ID
        interception: {//源截取
          show: false,
          windowId: 0,
          inputSourceId: 0
        },
        layout: {//是否显示布局
          show: false,
          windowId: 0
        },
        saveScenarist: false,
        selectShow: false,
        ctext: '',
        current: {},
        activeTab: 1,
        outSwiper: null,
        sourceSwiper: null,
        osdSwiper: null,
        sourceLen: 0,
        osdLen: 0,
        pageNnm: 4,
        osdlistLen: 0,
        sourceListLen: 0
      };
    },
    watch: {
      'zIndexMenuOption.activeSource'(val, oldVal) {
        if(val >= 0 && val < this.openedSources.length) {
          this.zIndexMenuOption.isLock = this.openedSources[val].isLock;
        }
      },
      openedSources: {
        deep: true,
        handler: function(val, oldVal) {
          if(val.length) {
            let source = val[this.zIndexMenuOption.activeSource];
            this.layer.width = Math.round(source.w / this.getVirtualDom.wScale);
            this.layer.height = Math.round(source.h / this.getVirtualDom.hScale);
            this.layer.x = Math.round(source.x / this.getVirtualDom.wScale);
            this.layer.y = Math.round(source.y / this.getVirtualDom.hScale);
            this.layer.winId = source.winId;//winId
            this.layer.type = source.sourceType;
            this.layer.id = source.id;
            this.layer.zIndex = source.zIndex;
          } else {
            this.layer.x = '';
            this.layer.y = '';
            this.layer.width = '';
            this.layer.height = '';
          }
        }
      }
    },
    computed: {
      /**获取源宽高度和窗口的比例 */
      getVirtualDom() {
        let wScale = this.sourceWindow.w / this.layer.showWidth;
        let hScale = this.sourceWindow.h / this.layer.showHeight;
        return {
          wScale,
          hScale
        };
      },
      //将测试数据显示到屏幕上
      showConsole() {
        let reserve = Object.assign([], this.eventNames);
        return reserve.reverse();
      },
      //是否拖拽新增源
      panAbel() {
        return this.getIsHScreen ? ['panleft', 'panright'] : ['panup', 'pandown'];
      }
    },
    methods: {
      //获取源的状态
      getInputSourceStatusById(id) {
        return this.inputSources.filter(source => {
          return source.id === id;
        })[0];
      },
      selecteTad(type) {
        this.activeTab = type;
        if(type == 1) {
          this.outSwiper.slideTo(0, 300, false);//切换到第一个slide，速度为0.3秒
          this.sourceSwiper.slideTo(0, 100, false);
        } else {
          this.outSwiper.slideTo(1, 300, false);//切换到第一个slide，速度为0.3秒
          this.osdSwiper.slideTo(0, 100, false);
        }
      },
      //获取元素距离屏幕的位置
      getPosition(dom) {
        if(!dom) {
          return {};
        }
        return {
          ox: dom.getBoundingClientRect().left,
          oy: dom.getBoundingClientRect().top,
          ow: dom.clientWidth,
          oh: dom.clientHeight,
          x: dom.getBoundingClientRect().left,
          y: dom.getBoundingClientRect().top,
          w: dom.clientWidth,
          h: dom.clientHeight
        };
      },
      //在屏幕上显示测试数据
      addToConsole(eventName) {
        if(this.eventNames.length) {
          if(this.eventNames[this.eventNames.length - 1].name === eventName) {
            this.eventNames[this.eventNames.length - 1].count++;
          } else {
            this.eventNames.push({
              name: eventName,
              count: 1
            });
          }
        } else {
          this.eventNames.push({
            name: eventName,
            count: 1
          });
        }
      },
      //碰撞检测
      collisionD(item1, item2, isFixed) {
        let fixedFlag = (item2.y + item2.h > item1.y) && (item2.y < item1.y + item1.h) && (item2.x + item2.w > item1.x) && (item2.x < item1.x + item1.w);
        let absoluteFlag = (item2.y + item2.h > 0) && (item2.y < item1.h) && (item2.x + item2.w > 0) && (item2.x < item1.w);
        return isFixed ? fixedFlag : absoluteFlag;
      },
      //事件分发(窗口中操作)
      handleSourceInWindow(eventName, e, hammer) {
        let tempEvent = eventName;
        if(tempEvent.match(/pan|press|pinch/)) {
          tempEvent = tempEvent.match(/pan|press|pinch/)[0];
        }

        this.hideBtns();

        switch(tempEvent) {
          case 'tap': this.tapSource(eventName, e, hammer); return false;
          case 'doubletap': this.doubleTapSource(eventName, e, hammer); return false;
          case 'pan': this.moveSource(eventName, e, hammer); return false;
          case 'press': this.longPressSource(eventName, e, hammer); return false;
          case 'pinch': this.pinchSource(eventName, e, hammer); return false;
          default: console.log('没有此事件');
        }
      },
      //事件分发(源列表中操作)
      handleSource(eventName, e, hammer) {
        let tempEvent = eventName;
        if(tempEvent.match(/pan|press/)) {
          tempEvent = tempEvent.match(/pan|press/)[0];
        }
        switch(tempEvent) {
          case 'doubletap': this.openSourceByClick(eventName, e, hammer); return false;
          case 'pan': this.openSourceByPan(eventName, e, hammer); return false;
          case 'tap': this.activeSwiperSource(eventName, e, hammer); return false;
          default: console.log('没有此事件');
        }
      },
      //左上角操作事件分发
      handleSourceLT(eventName, e, hammer) {
        let tempEvent = eventName;
        if(tempEvent.match(/pan/)) {
          tempEvent = tempEvent.match(/pan/)[0];
        }

        switch(tempEvent) {
          case 'tap': this.maxInWindow(eventName, e, hammer); return false;
          case 'doubletap': this.doubleTapSource(eventName, e, hammer); return false;
          case 'pan': this.moveSource(eventName, e, hammer); return false;
          default: console.log('没有此事件');
        }
      },
      //双击新开窗、新增一个源
      openSourceByClick(eventName, e, hammer) {
        this.assemblySourceData(hammer);
        this.enteringSource.show = false;
        if(this.inputSources[hammer.index].winId) {
          this.enteringSource.winId = this.inputSources[hammer.index].winId;
        }
        let _source = Object.assign({}, this.enteringSource, {
          ox: 0,
          oy: 0,
          ow: this.sourceWindow.w,
          oh: this.sourceWindow.h,
          x: 0,
          y: 0,
          w: this.sourceWindow.w,
          h: this.sourceWindow.h,
          zIndex: this.zIndexMenuOption.zIndex++
        });
        if(_source.sourceType === 'osd') {
          _source.ow = _source.w = this.inputSources[hammer.index].w;
          _source.oh = _source.h = this.inputSources[hammer.index].h;
        }
        this.addSource(_source);
      },
      //获取源中心可替换区域的位置
      getAbelReplaceWindowArea(item) {
        return {
          x: item.x + item.w / 2 - this.constOption.minSourceLength / 2,
          y: item.y + item.h / 2 - this.constOption.minSourceLength / 2,
          w: this.constOption.minSourceLength,
          h: this.constOption.minSourceLength
        };
      },
      //源中心可替换区域是否可见
      isAbleReplaceWindowVisible(index) {
        let tempOpenedSources = Object.assign([], this.openedSources).sort((a, b) => a.zIndex < b.zIndex);
        let target = this.getAbelReplaceWindowArea(tempOpenedSources[index]);
        let result = true;
        if(index < tempOpenedSources.length) {
          tempOpenedSources.forEach((item, i) => {
            if(index > i) {
              if(target.x > tempOpenedSources[i].x && target.x + target.w < tempOpenedSources[i].x + tempOpenedSources[i].w && target.y > tempOpenedSources[i].y && target.y + target.h < tempOpenedSources[i].y + tempOpenedSources[i].h) {
                result = false;
              }
            }
          });
        }
        return result;
      },
      //是否在任何一个非锁定窗的中心可替换区域
      isInAbleReplaceWindow(target) {
        let winId = '';
        Object.assign([], this.openedSources.filter(({ sourceType }) => target.sourceType === sourceType)).sort((a, b) => a.zIndex < b.zIndex).forEach((item, index) => {
          //窗口不能锁定，target必须与源中心可替换区域重叠，且源中心可替换区域必须可见
          if(!item.isLock && this.collisionD(this.getAbelReplaceWindowArea(item), target, true) && this.isAbleReplaceWindowVisible(index)) {
            if(!winId) {
              winId = item.winId;
            }
          }
        });
        return winId;
      },
      //拖拽新增一个源
      openSourceByPan(eventName, e, hammer) {
        if(eventName !== 'panstart') {
          this.zIndexMenuOption.panStatus++;
        } else {
          this.zIndexMenuOption.panStatus = 0;
        }
        if(this.zIndexMenuOption.panStatus === 1 && this.panAbel.indexOf(eventName) === -1) {
          return false;
        }
        if(eventName === 'panstart') {
          this.zIndexMenuOption.swiperActiveIndex = hammer.index;
        } else if(eventName === 'pancancel') {
          this.enteringSource = Object.assign({}, this.initSource);
        } else if(eventName === 'panend') {
          let _source = Object.assign({}, this.enteringSource, {
            x: this.enteringSource.x - this.sourceWindow.x,
            y: this.enteringSource.y - this.sourceWindow.y,
            w: this.inputSources[hammer.index].w,
            h: this.inputSources[hammer.index].h,//真实的高度像素
            ox: this.enteringSource.x - this.sourceWindow.x,
            oy: this.enteringSource.y - this.sourceWindow.y,
            ow: this.inputSources[hammer.index].w,
            oh: this.inputSources[hammer.index].h,
            zIndex: this.zIndexMenuOption.zIndex++
          });
          // 新增或替换
          if(this.openedSources.findIndex(item => item.replaceStatus) !== -1) { //替换源
            this.replaceSource(_source);
          } else if(this.collisionD(this.sourceWindow, this.enteringSource, true)) { //新开窗
            //最多允许开窗限制
            this.addSource(_source, true);
          }
          this.enteringSource = Object.assign({}, this.initSource);
        } else {
          if(this.zIndexMenuOption.panStatus === 1 && this.panAbel.indexOf(eventName) !== -1) {
            this.assemblySourceData(hammer);
            this.enteringSource.show = true;
          }
          this.enteringSource.x = this.enteringSource.ox + e.deltaX;
          this.enteringSource.y = this.enteringSource.oy + e.deltaY;

          let tempSource = Object.assign({}, this.enteringSource, {
            x: this.enteringSource.x - this.sourceWindow.x,//相对于源窗口的left值
            y: this.enteringSource.y - this.sourceWindow.y,//相对于源窗口的top值
            w: this.enteringSource.w,//真实的宽度像素
            h: this.enteringSource.h//真实的高度像素
          });

          let winId = this.isInAbleReplaceWindow(tempSource);
          this.openedSources.forEach(item => {
            item.replaceStatus = item.winId === winId;
          });

          let enteringSourceType = this.enteringSource.sourceType;
          let openedSourceTypeLength = this.openedSources.filter(({ sourceType }) => sourceType === enteringSourceType).length;
          let isFull = enteringSourceType === 'normal' ? openedSourceTypeLength === this.winList.length : openedSourceTypeLength;
          this.enteringSource.fullSourceStatus = winId === '' && isFull;
        }
      },
      //单击选中swiper效果
      activeSwiperSource(eventName, e, hammer) {
        this.zIndexMenuOption.swiperActiveIndex = hammer.index;
      },
      //窗口中点击一个源
      tapSource(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        //点击400毫秒内移除元素会导致数组元素消失，根据index取不到或者取到上一个源 TODO
        if(currentSource) {
          this.zIndexMenuOption.activeSource = hammer.index;
          currentSource.count++;
        }
      },
      //窗口中双击一个源
      doubleTapSource(eventName, e, hammer) {
        let targetSource = this.openedSources[hammer.index];
        this.tapSource(eventName, e, hammer);
        if(targetSource.isLock) {
          return false;
        }
        if(targetSource.sourceType !== 'osd') {
          let unitWidth = this.sourceWindow.w / this.lay.cols;
          let unitHeight = this.sourceWindow.h / this.lay.rows;
          let tempPosition = {};
          tempPosition.y = Math.max(0, Math.floor(targetSource.y / unitHeight) * unitHeight);
          tempPosition.h = Math.min(Math.ceil((targetSource.y + targetSource.h) / unitHeight) * unitHeight - tempPosition.y, this.sourceWindow.h - tempPosition.y);
          tempPosition.x = Math.max(0, Math.floor(targetSource.x / unitWidth) * unitWidth);
          tempPosition.w = Math.min(Math.ceil((targetSource.x + targetSource.w) / unitWidth) * unitWidth - tempPosition.x, this.sourceWindow.w - tempPosition.x);
          tempPosition.oy = tempPosition.y;
          tempPosition.oh = tempPosition.h;
          tempPosition.ox = tempPosition.x;
          tempPosition.ow = tempPosition.w;
          Object.assign(targetSource, tempPosition);
          this.setWinParam();
        }
      },
      //窗口中长按一个源
      longPressSource(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        if(currentSource.sourceType !== 'osd') {
          this.tapSource(eventName, e, hammer);

          if(currentSource.isLock) {
            return false;
          }

          this.zIndexMenuOption.operateBtnGroupIndex = hammer.index;
          this.zIndexMenuOption.langPressX = e.center.x - this.sourceWindow.x;
          this.zIndexMenuOption.langPressY = e.center.y - this.sourceWindow.y;
        }
      },
      //在窗口中移动源
      moveSource(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        if(currentSource.isLock) {
          return false;
        }

        if(eventName === 'panstart') {
          this.tapSource(eventName, e, hammer);
        } else if(eventName === 'pancancel') {
        } else if(eventName === 'panend') {
          if(this.collisionD(this.sourceWindow, currentSource)) {
            currentSource.ox = currentSource.x;
            currentSource.oy = currentSource.y;
            this.setWinParam();
          } else {
            this.deleteSource(eventName, e, hammer);
          }
        } else {
          currentSource.x = currentSource.ox + e.deltaX;
          currentSource.y = currentSource.oy + e.deltaY;
        }
      },
      //缩放一个源
      pinchSource(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        if(currentSource.isLock) {
          return false;
        }

        if(currentSource.sourceType !== 'osd') {
          if(eventName === 'pinchstart') {
            clearTimeout(timmer);
            hammer.get('pan').set({ enable: false });
          }
          if(eventName === 'pinchmove') {
            clearTimeout(timmer);
            currentSource.w = currentSource.ow * e.scale;
            currentSource.h = currentSource.oh * e.scale;
            currentSource.w = Math.min(Math.max(currentSource.w, this.constOption.minSourceLength), this.sourceWindow.w);
            currentSource.h = Math.min(Math.max(currentSource.h, this.constOption.minSourceLength), this.sourceWindow.h);
            currentSource.x = currentSource.ox - (currentSource.w - currentSource.ow) / 2;
            currentSource.y = currentSource.oy - (currentSource.h - currentSource.oh) / 2;
          }

          if(eventName === 'pinchend' || eventName === 'pinchcancel') {
            if(this.collisionD(this.sourceWindow, currentSource)) {
              currentSource.ow = currentSource.w;
              currentSource.oh = currentSource.h;
              currentSource.ox = currentSource.x;
              currentSource.oy = currentSource.y;
              this.setWinParam();
            } else {
              this.deleteSource(eventName, e, hammer);
            }
          }

          if(['pinchout', 'pinchcancel', 'pinchend'].indexOf(eventName) != -1) {
            timmer = setTimeout(() => {
              hammer.get('pan').set({ enable: true });
            }, 300);
          }
        }

      },
      //左上角按钮单击放大全屏
      maxInWindow(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        currentSource.x = 0;
        currentSource.y = 0;
        currentSource.w = this.sourceWindow.w;
        currentSource.h = this.sourceWindow.h;
        currentSource.ox = 0;
        currentSource.oy = 0;
        currentSource.ow = this.sourceWindow.w;
        currentSource.oh = this.sourceWindow.h;
        //设置窗口位置
        this.setWinParam();
      },
      //关闭一个窗
      deleteSource(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        this.openedSources.splice(hammer.index, 1);

        if(this.zIndexMenuOption.activeSource > 0) {
          this.zIndexMenuOption.activeSource--;
        }
        if(currentSource.sourceType === 'osd') {
          let _source = {
            winId: +currentSource.winId,
            priority: +currentSource.zIndex,
            imageId: +currentSource.id,
            startX: +currentSource.x,
            startY: +currentSource.y,
            status: 0
          };
          this.setOsdParam(_source);
        } else {
          this.setWinStatus({
            windowId: currentSource.winId,
            windowStatus: 0
          });
        }
      },
      //右下角伸缩
      scaleR(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        if(eventName === 'panstart') {
        } else if(eventName === 'pancancel') {
        } else if(eventName === 'panend') {
          currentSource.ow = currentSource.w;
          currentSource.oh = currentSource.h;
          //设置窗口位置
          this.setWinParam();
        } else {
          if(currentSource.ow + e.deltaX > this.constOption.minSourceLength) {
            currentSource.w = currentSource.ow + e.deltaX;
          }
          if(currentSource.oh + e.deltaY > this.constOption.minSourceLength) {
            currentSource.h = currentSource.oh + e.deltaY;
          }
        }
      },
      //左下角伸缩
      scaleL(eventName, e, hammer) {
        let currentSource = this.openedSources[hammer.index];
        if(eventName === 'panstart') {
          //转换为靠右对齐
          currentSource.isAlignRight = true;
          currentSource.x = this.sourceWindow.w - currentSource.x - currentSource.w;
        } else if(eventName === 'pancancel') {
          currentSource.isAlignRight = false;
          currentSource.x = this.sourceWindow.w - currentSource.x - currentSource.w;
        } else if(eventName === 'panend') {
          currentSource.isAlignRight = false;
          currentSource.ow = currentSource.w;
          currentSource.oh = currentSource.h;
          currentSource.x = this.sourceWindow.w - currentSource.x - currentSource.w;
          currentSource.ox = currentSource.x;
          this.setWinParam();
        } else {
          if(currentSource.ow - e.deltaX > this.constOption.minSourceLength) {
            currentSource.w = currentSource.ow - e.deltaX;
          }
          if(currentSource.oh + e.deltaY > this.constOption.minSourceLength) {
            currentSource.h = currentSource.oh + e.deltaY;
          }
        }
      },
      //源层级向上移动一个
      prev() {
        if(this.openedSources.length > 1) {
          if(this.checkSourceType(this.openedSources[this.zIndexMenuOption.activeSource])) {
            return false;
          }
          let temp = this.openedSources.filter(item => {
            return item.sourceType !== 'osd';
          }).filter(item => {
            return item.zIndex > this.openedSources[this.zIndexMenuOption.activeSource].zIndex;
          }).min('zIndex');
          if(temp) {
            let tempIndex = temp.zIndex;
            temp.zIndex = this.openedSources[this.zIndexMenuOption.activeSource].zIndex;
            this.openedSources[this.zIndexMenuOption.activeSource].zIndex = tempIndex;
            this.htpZindex();
          }
        }
      },
      //源层级向下移动一个
      next() {
        if(this.openedSources.length > 1) {
          if(this.checkSourceType(this.openedSources[this.zIndexMenuOption.activeSource])) {
            return false;
          }
          let temp = this.openedSources.filter(item => {
            return item.sourceType !== 'osd';
          }).filter(item => {
            return item.zIndex < this.openedSources[this.zIndexMenuOption.activeSource].zIndex;
          }).max('zIndex');
          if(temp) {
            let tempIndex = temp.zIndex;
            temp.zIndex = this.openedSources[this.zIndexMenuOption.activeSource].zIndex;
            this.openedSources[this.zIndexMenuOption.activeSource].zIndex = tempIndex;
            this.htpZindex();
          }
        }
      },
      //源层级移到最顶
      first() {
        if(this.openedSources.length > 1) {
          if(this.checkSourceType(this.openedSources[this.zIndexMenuOption.activeSource])) {
            let activeSource = this.openedSources[this.zIndexMenuOption.activeSource];
            let sources = this.openedSources.filter(item => {
              return item.zIndex > activeSource.zIndex;
            });
            if(sources.length) {
              this.openedSources[this.zIndexMenuOption.activeSource].zIndex = 999;
              let source = {
                winId: +this.openedSources[this.zIndexMenuOption.activeSource].winId,
                priority: 1,
                imageId: +this.openedSources[this.zIndexMenuOption.activeSource].id,
                startX: Math.round(+this.openedSources[this.zIndexMenuOption.activeSource].x / this.getVirtualDom.wScale),
                startY: Math.round(+this.openedSources[this.zIndexMenuOption.activeSource].y / this.getVirtualDom.hScale),
                status: 1
              };
              this.setOsdParam(source);
            }
          } else {
            let temp = this.openedSources.filter(source => {
              return source.sourceType !== 'osd';
            }).max('zIndex');
            if(temp) {
              let tempIndex = temp.zIndex;
              if(this.openedSources[this.zIndexMenuOption.activeSource].zIndex !== tempIndex) {
                this.openedSources[this.zIndexMenuOption.activeSource].zIndex = ++tempIndex;
                this.htpZindex();
              }
            }
          }
        }
      },
      //源层级移到最底
      last() {
        if(this.openedSources.length > 1) {
          if(this.checkSourceType(this.openedSources[this.zIndexMenuOption.activeSource])) {
            let temp = this.openedSources.min('zIndex');
            if(temp) {
              let tempIndex = temp.zIndex;
              if(this.openedSources[this.zIndexMenuOption.activeSource].zIndex !== tempIndex) {
                this.openedSources.filter(source => {
                  return source.sourceType !== 'osd';
                }).forEach(item => {
                  item.zIndex++;
                });
                this.zIndexMenuOption.zIndex++;
                this.openedSources[this.zIndexMenuOption.activeSource].zIndex = 1;
                let source = {
                  winId: +this.openedSources[this.zIndexMenuOption.activeSource].winId,
                  priority: 0,
                  imageId: +this.openedSources[this.zIndexMenuOption.activeSource].id,
                  startX: Math.round(+this.openedSources[this.zIndexMenuOption.activeSource].x / this.getVirtualDom.wScale),
                  startY: Math.round(+this.openedSources[this.zIndexMenuOption.activeSource].y / this.getVirtualDom.hScale),
                  status: 1
                };
                this.setOsdParam(source);
              }
            }
          } else {
            this.openedSources.filter(source => {
              return source.sourceType !== 'osd';
            }).forEach(item => {
              item.zIndex++;
            });
            this.zIndexMenuOption.zIndex++;
            let temp = this.openedSources.filter(source => {
              return source.sourceType !== 'osd';
            }).min('zIndex');
            if(temp) {
              let tempIndex = temp.zIndex;
              if(this.openedSources[this.zIndexMenuOption.activeSource].zIndex !== tempIndex) {
                this.openedSources[this.zIndexMenuOption.activeSource].zIndex = --tempIndex;
                this.openedSources.forEach(item => {
                  item.zIndex++;
                });
                this.htpZindex();
              }
            }
          }
        }
      },
      //置顶置底Z序调整
      htpZindex() {
        let params = [];

        Object.assign([], this.openedSources.filter(source => {
          return source.sourceType !== 'osd';
        })).sort((a, b) => {
          return a.zIndex - b.zIndex;
        }).map((source, i) => {
          params.push({
            id: source.winId,
            priority: i + 1
          });
        });
        this.setWinZOrder({ winList: params });
      },
      //重置
      reset() {
        if(this.openedSources.length) {
          if(this.layer.showHeight > this.constOption.defaultSourceWidth && this.layer.showWidth > this.constOption.defaultSourceHeight) {
            this.openedSources.filter(item => !item.isLock).forEach((item, index) => {

              item.x = index * 20;
              item.y = index * 20;
              item.ow = item.w;
              item.oh = item.h;
              item.ox = item.x;
              item.oy = item.y;
              if(item.sourceType !== 'osd') {
                item.w = this.constOption.defaultSourceWidth * this.getVirtualDom.wScale;
                item.h = this.constOption.defaultSourceHeight * this.getVirtualDom.hScale;
              }
            });
          } else {
            this.openedSources.filter(item => !item.isLock).forEach(item => {
              item.x = 0;
              item.y = 0;
              item.ox = 0;
              item.oy = 0;
              if(item.sourceType !== 'osd') {
                item.ow = item.w = this.sourceWindow.w;
                item.oh = item.h = this.sourceWindow.h;
              }
            });
          }

          this.openedSources.filter(item => !item.isLock).forEach(item => {
            if(item.sourceType === 'osd') {
              let source = {
                winId: item.winId,
                priority: item.zIndex,
                imageId: item.id,
                startX: Math.round(item.x / this.getVirtualDom.wScale),
                startY: Math.round(item.y / this.getVirtualDom.hScale),
                status: 1
              };
              this.setOsdParam(source);
            } else {
              htp.post(urls.setWinParam, {
                windowId: item.winId,
                windowStartX: Math.round(item.x / this.getVirtualDom.wScale),
                windowStartY: Math.round(item.y / this.getVirtualDom.hScale),
                windowWidth: Math.round(item.w / this.getVirtualDom.wScale),
                windowHeight: Math.round(item.h / this.getVirtualDom.hScale)
              });
            }
          });
        }
      },
      //清空窗
      closeWindows() {
        let unLockWins = this.openedSources.filter(item => !item.isLock);
        if(unLockWins.length) {
          let unLockOSDWin = unLockWins.filter(item => item.sourceType === 'osd');
          if(unLockOSDWin.length) {
            let source = {
              winId: unLockOSDWin[0].winId,
              priority: unLockOSDWin[0].zIndex,
              imageId: unLockOSDWin[0].id,
              startX: unLockOSDWin[0].x,
              startY: unLockOSDWin[0].y,
              status: 0
            };
            this.setOsdParam(source);
          }

          htp.post(urls.setAllWinClose, { closeAllWin: 1 }).then(res => {
            if(res.status === 10000000) {
              this.openedSources = this.openedSources.filter(item => item.isLock);
              this.zIndexMenuOption.activeSource = 0;
              this.winList.forEach(item => {
                item.status = Number(this.openedSources.findIndex(item1 => item1.id === item.id) != -1);
              });
            }
          });

        }
      },
      //锁定/释放源
      lock() {
        if(this.openedSources.length) {
          let currentSource = this.openedSources[this.zIndexMenuOption.activeSource];
          if(currentSource.sourceType === 'osd') {
            let source = {
              osdId: currentSource.winId,
              isLocked: Number(!this.zIndexMenuOption.isLock)
            };
            this.setOsdLockStatus(source);
          } else {
            htp.post(urls.setWinLock, {
              windowId: currentSource.winId,
              isLocked: Number(!this.zIndexMenuOption.isLock)
            }).then(res => {
              if(res.status === 10000000) {
                this.zIndexMenuOption.isLock = !this.zIndexMenuOption.isLock;
                this.openedSources[this.zIndexMenuOption.activeSource].isLock = this.zIndexMenuOption.isLock;
              }
            });
          }

        }
      },
      setOsdLockStatus(param) {
        htp.post(urls.setOsdLockStatus, param).then(res => {
          if(res.status === 10000000) {
            this.zIndexMenuOption.isLock = !this.zIndexMenuOption.isLock;
            this.openedSources[this.zIndexMenuOption.activeSource].isLock = this.zIndexMenuOption.isLock;
          }
        });
      },
      //开窗
      setWinStatus(param) {
        htp.post(urls.setWinStatus, param).then(res => {
          if(res.status === 10000000) {
            this.winList.map(win => {
              if(win.id == param.windowId) {
                win.status = param.windowStatus;
              }
            });
          }
        });
      },
      //窗口位置参数设置
      setWinParam(cb) {
        setTimeout(() => {
          if(this.layer.type === 'osd') {
            let source = {
              winId: +this.layer.winId,
              priority: +this.layer.zIndex,
              imageId: +this.layer.id,
              startX: +this.layer.x,
              startY: +this.layer.y,
              status: 1
            };
            if(this.layer.zIndex >= 999) {
              source.priority = 1;
            } else {
              source.priority = 0;
            }
            this.setOsdParam(source);
          } else {
            htp.post(urls.setWinParam, {
              windowId: +this.layer.winId,
              windowStartX: +this.layer.x,
              windowStartY: +this.layer.y,
              windowWidth: +this.layer.width,
              windowHeight: +this.layer.height
            }).then(res => {
              if(res.status === 10000000) {
                cb && cb();
              }
            });
          }
        }, 500);
      },
      setOsdParam(source) {
        htp.post(urls.setOsdParam, {
          id: 0,
          status: source.status,
          priority: source.priority,
          imageId: source.imageId,
          startX: source.startX,
          startY: source.startY
        });
      },
      //设置窗口输入源
      setWinInput(param) {
        htp.post(urls.setWinInput, param).then(res => {
          if(res.status === 10000000) {
            this.winList.map(win => {
              if(win.id == param.selectWindow) {
                win.inputSourceId = param.selectInput;
              }
            });
          }
        });
      },
      //隐藏源截取按钮组
      hideBtns() {
        this.zIndexMenuOption.operateBtnGroupIndex = -1;
      },
      //源截取
      cutSource() {
        let source = this.openedSources[this.zIndexMenuOption.operateBtnGroupIndex];
        this.$router.push({ name: 'cut', params: { winId: source.winId, inputSourceId: source.id } });
      },
      //布局
      onLayout() {
        let source = this.openedSources[this.zIndexMenuOption.operateBtnGroupIndex];
        this.$router.push({ name: 'scenaristLayout', params: { winId: source.winId, source: source } });
      },
      //设置Z序
      setWinZOrder(param) {
        htp.post(urls.setWinZOrder, param);
      },
      //重新载入
      reload() {
        this.openedSources = [];
        this.zIndexMenuOption = {
          isLock: false, //锁定图标
          zIndex: 1, //打开的源的层级顺序
          activeSource: 0, //当前激活的源
          swiperActiveIndex: -1, //拖拽新增的源下标
          isShowTake: true, //显示take按钮
          operateBtnGroupIndex: -1,
          panStatus: 0
        };
        this.initLayer();
      }
    }
  };
</script>

<style lang="less">
  body {
    overflow: hidden;
  }
  .source-item-use,
  .source-entering,
  .swiper-slide {
    position: absolute;
    .device_icon {
      width: 100%;
      height: 52%;
      background-position: center bottom;
      background-size: 80px;
    }
    .title {
      color: #fff;
      font-size: 24px;
      padding-top: 10px;
    }
  }
  .source-item-use {
    position: absolute;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    &.bgm-8 {
      background-color: #c16e6e;
      background-color: rgba(193, 110, 110, 0.5);
      background-image: url('../../assets/source_icons/osdbg.png');
    }
    &.bgm-0 {
      background-color: rgba(195, 125, 193, 0.5);
      background-image: url('../../assets/source_icons/source-bg1.png');
    }
    &.bgm-1 {
      background-color: rgba(119, 126, 210, 0.5);
      background-image: url('../../assets/source_icons/source-bg2.png');
    }
    &.bgm-2 {
      background-color: rgba(167, 119, 216, 0.5);
      background-image: url('../../assets/source_icons/source-bg3.png');
    }
    &.bgm-3 {
      background-color: rgba(204, 152, 152, 0.5);
      background-image: url('../../assets/source_icons/source-bg4.png');
    }
    &.bgm-4 {
      background-color: rgba(56, 163, 154, 0.5);
      background-image: url('../../assets/source_icons/source-bg5.png');
    }
    &.bgm-5 {
      background-color: rgba(89, 157, 210, 0.5);
      background-image: url('../../assets/source_icons/source-bg6.png');
    }
    &.bgm-6 {
      background-color: rgba(202, 164, 99, 0.5);
      background-image: url('../../assets/source_icons/source-bg7.png');
    }
    &.bgm-7 {
      background-color: rgba(65, 67, 75, 0.5);
      background-image: url('../../assets/source_icons/source-bg8.png');
    }
    &.active {
      outline: 1px dashed #e67007; /*no*/
      outline-offset: -1px; /*no*/
    }
    &.align-right {
      left: initial !important;
    }
    .replace-outline {
      position: absolute;
      width: 36px; /*no*/
      height: 36px; /*no*/
      outline: 2px solid #0ceb46 !important; /*no*/
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    .move-btns {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
    }
    .move-btn {
      position: absolute;
      width: 0.64rem;
      height: 0.64rem;
      border-radius: 50%;
      background-position: center;
      background-size: cover;
      &.btnLt {
        left: -16px;
        top: -16px;
        background-image: ~"url('~assets/common/left-top.png')";
      }
      &.btnRt {
        right: -16px;
        top: -16px;
        background-image: ~"url('~assets/common/right-top.png')";
      }
      &.btnRb {
        right: -16px;
        bottom: -16px;
        background-image: ~"url('~assets/common/right-bottom.png')";
      }
      &.btnLb {
        left: -16px;
        bottom: -16px;
        background-image: ~"url('~assets/common/left-bottom.png')";
      }
    }
  }
  .operator-btns {
    position: absolute;
    background-color: #0b0b0b;
    border-radius: 8px;
    height: 65px;
    min-width: 260px;
    z-index: 1050;
    transform: translateX(-50%);
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 0;
      height: 0;
      border-left: 16px solid transparent;
      border-right: 16px solid transparent;
      border-top: 16px solid #0b0b0b;
      left: 50%;
      transform: translateX(-50%);
    }
    .operator-btn {
      padding: 0px 30px;
      height: 65px;
      line-height: 65px;
      display: inline-block;
      border-left: 1px solid #424242; /*no*/
      color: #f8f8f8;
      font-size: 24px;
      &:first-child {
        border-left: 0;
      }
    }
    .dot {
      position: absolute;
      left: 50%;
      top: 100%;
      border-width: 0.2rem;
      border-style: solid;
      border-color: #0b0b0b transparent transparent transparent;
    }
  }
  .source-entering {
    position: fixed;
    border-radius: 5px; /*no*/
    background-color: #2e2e34;
  }
  .lock {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    background-image: ~"url('../../assets/common/big-lock.png')";
    background-size: 51px 57px; /* no*/
    background-repeat: no-repeat;
    background-position: center;
  }
  .dvi,
  .dualdvi {
    background: ~"url('../../assets/source_icons/dvi.png')" no-repeat;
  }
  .dvi_active,
  .dualdvi_active {
    background: ~"url('../../assets/source_icons/dvi_active.png')" no-repeat;
  }
  .hdmi,
  .hdmi1,
  .hdmi2 {
    background: ~"url('../../assets/source_icons/hdmi.png')" no-repeat;
  }
  .hdmi_active,
  .hdmi1_active,
  .hdmi2_active {
    background: ~"url('../../assets/source_icons/hdmi_active.png')" no-repeat;
  }
  .vga {
    background: ~"url('../../assets/source_icons/vga.png')" no-repeat;
  }
  .vga_active {
    background: ~"url('../../assets/source_icons/vga_active.png')" no-repeat;
  }
  .sdi {
    background: ~"url('../../assets/source_icons/sdi.png')" no-repeat;
  }
  .sdi_active {
    background: ~"url('../../assets/source_icons/sdi_active.png')" no-repeat;
  }
  .cvbs {
    background: ~"url('../../assets/source_icons/cvbs.png')" no-repeat;
  }
  .cvbs_active {
    background: ~"url('../../assets/source_icons/cvbs_active.png')" no-repeat;
  }
  .dualdvi {
    background: ~"url('../../assets/source_icons/dvi.png')" no-repeat;
  }
  .dualdvi_active {
    background: ~"url('../../assets/source_icons/dvi_active.png')" no-repeat;
  }
  .dp1,
  .dp2 {
    background: ~"url('../../assets/source_icons/dp.png')" no-repeat;
  }
  .dp1_active,
  .dp2_active {
    background: ~"url('../../assets/source_icons/dp_active.png')" no-repeat;
  }
  .usb {
    background: ~"url('../../assets/source_icons/usb.png')" no-repeat;
  }
  .usb_active {
    background: ~"url('../../assets/source_icons/usb_active.png')" no-repeat;
  }
  .HDbaseT {
    background: ~"url('../../assets/source_icons/wz.png')" no-repeat;
  }
  .HDbaseT_active {
    background: ~"url('../../assets/source_icons/wz_active.png')" no-repeat;
  }
  .osd,
  .osd_active {
    background: ~"url('../../assets/source_icons/osd_icon.png')" no-repeat;
  }
  .osdtext,
  .osdtext_active {
    background: ~"url('../../assets/source_icons/bdosdtext.png')" no-repeat;
  }
  .dangrous {
    outline: 1px solid #d24949 !important; /*no*/
  }
</style>



