<script>
  import htp from "../../api/http";
  import urls from '../../api/apiurl';
  import { Toast } from 'mint-ui';
  export default {
    methods: {
      //添加一个源
      addSource(source, replace) {
        switch(source.sourceType) {
          case 'osd':
            if(this.checkSource(source.sourceType, 1)) {
              source.winId = this.osdList[0].id;
              source.zIndex = 999;
              source.colorIndex = 8;
              this.openedSources.push(source);
              this.zIndexMenuOption.activeSource = this.openedSources.length - 1;
              this.openOSDWin(source, 1);
            }
            return;
          case 'normal':
            if(this.checkSource(source.sourceType, this.winList.length)) {
              let openedWins = this.checkOpenedWin(this.winList);
              if(openedWins.length) {
                if(replace) {
                  if(this.layer.showHeight < this.constOption.defaultSourceWidth || this.layer.showWidth < this.constOption.defaultSourceHeight) {
                    source.w = this.sourceWindow.w;
                    source.h = this.sourceWindow.h;
                    source.x = 0;
                    source.y = 0;
                    source.ow = source.w;
                    source.oh = source.h;
                    source.ox = 0;
                    source.oy = 0;
                  }
                }
                source.winId = openedWins[0].id;
                this.colorHandler();
                source.colorIndex = this.colorIndex;
                this.colorIndex++;
                this.openedSources.push(source);
                this.zIndexMenuOption.activeSource = this.openedSources.length - 1;
                this.openWin(source);
              } else {
                Toast(this.$t('common.sceneMsg.limit'));
              }
            }
            return;
          default: break;
        }
      },
      colorHandler() {
        if(this.colorIndex > this.colors.length - 3) {
          this.colorIndex = 0;
        }
      },
      //根据源的类型判断源是否可以添加
      checkSource(sourceType, len) {
        if(this.openedSources.filter(e => {
          return e.sourceType === sourceType;
        }).length >= len) {
          Toast(len == 1 ? this.$t('common.sceneMsg.osdlimit') : this.$t('common.sceneMsg.limit'));
          return false;
        }
        return true;
      },
      checkOpenedWin(windows) {
        return windows.filter(win => {
          return win.status === 0;
        });
      },
      //替换一个源
      replaceSource(source) {
        let result = this.checkReplace(source);
        if(result.isReplace) {
          switch(source.sourceType) {
            case 'osd':
              Object.assign(result.replaceSource, {
                id: source.id,
                type: source.type,
                name: source.name,
                status: source.status,
                replaceStatus: false,
                w: source.w,
                h: source.h,
                ow: source.ow,
                oh: source.oh
              });
              this.openOSDWin(source, 1);
              return;
            case 'normal':
              Object.assign(result.replaceSource, {
                id: source.id,
                type: source.type,
                name: source.name,
                status: source.status,
                replaceStatus: false
              });
              this.setWinInput({
                selectWindow: result.replaceSource.winId,
                selectInput: result.replaceSource.id
              });
              console.log('替换一个普通源成功');
              return;
            default: return;
          }
        }
      },
      checkReplace(source) {
        let ableToReplaceSource = this.openedSources.filter(e => e.replaceStatus);
        if(ableToReplaceSource.length) {
          if(ableToReplaceSource[0].sourceType === source.sourceType) {
            console.log('可以替换');
            return { replaceSource: ableToReplaceSource[0], isReplace: true };
          } else {
            console.log('类型不同不能替换');
            return { isReplace: false };
          }
        } else {
          Toast(this.$t('common.sceneMsg.lock'));
          return { isReplace: false };
        }

      },
      //组装源数据
      assemblySourceData(hammer) {
        this.enteringSource = Object.assign({}, this.initSource, this.getPosition(hammer.element));
        Object.assign(this.enteringSource, {
          name: this.inputSources[hammer.index].name,
          type: this.inputSources[hammer.index].type,
          id: this.inputSources[hammer.index].id,
          status: this.inputSources[hammer.index].status,
          sourceType: this.inputSources[hammer.index].sourceType
        });
      },
      //开窗下发命令
      openWin(source) {
        htp.post(urls.setWinBaseInfo, {
          windowId: source.winId,
          status: 1,
          inputSourceId: source.id,
          startX: Math.round(source.x / this.getVirtualDom.wScale),
          startY: Math.round(source.y / this.getVirtualDom.hScale),
          width: Math.round(source.w / this.getVirtualDom.wScale),
          height: Math.round(source.h / this.getVirtualDom.hScale),
          priority: this.winList.length
        }).then(res => {
          if(res.status === 10000000) {
            this.winList.filter(win => win.id === source.winId)[0].status = 1;
            console.log(this.winList);
          }
        });
      },
      //开OSD窗下发命令
      openOSDWin(source, status) {
        setTimeout(() => {
          let _source = {
            winId: 0,
            priority: 1,
            imageId: source.id,
            startX: +this.layer.x,
            startY: +this.layer.y,
            status: 1
          };
          this.setOsdParam(_source);
        }, 500);
      },
      checkSourceType(source) {
        if(source.sourceType === 'normal') {
          return false;
        } else {
          return true;
        }
      },
      getOsdById(id) {
        return this.inputSources.filter(source => {
          return source.sourceType === "osd" && source.id === id;
        });
      },
      getOsdList() {
        let scale = this.getVirtualDom;
        this.osdList.filter(layer => {
          return layer.status === 1;
        }).map(source => {
          let osd = this.getOsdById(source.inputSourceId)[0];
          let tempSource = Object.assign({}, source, {//初始化source
            ox: source.startX * scale.wScale, //上一次的x坐标
            oy: source.startY * scale.hScale, //上一次的y坐标
            ow: osd.width * scale.wScale, //上一次的宽度
            oh: osd.height * scale.hScale, //上一次的高度
            x: source.startX * scale.wScale, //x坐标
            y: source.startY * scale.hScale, //y坐标
            w: osd.width * scale.wScale, //宽度
            h: osd.height * scale.hScale, //高度
            show: false, //是否显示
            name: 'OSD' + (osd.id + 1), //源名称
            isAlignRight: false, //是否开启右侧对齐(左下角按钮操作时需开启右侧对齐)
            isLock: !!source.isLocked, //锁定源后不能移动和层级操作
            type: osd.type,//类型
            zIndex: source.priority, //Z序
            winId: source.id,
            status: source.status,
            replaceStatus: false,
            count: 0,
            sourceType: 'osd'
          });
          tempSource.id = source.inputSourceId;
          tempSource.winId = source.id;
          this.openedSources.push(tempSource);
        });
      },
      getInputSourceList() {
        let scale = this.getVirtualDom;
        this.winList.filter(layer => {
          return layer.status === 1;
        }).map(source => {
          let tempSource = Object.assign({}, source, {//初始化source
            ox: source.startX * scale.wScale, //上一次的x坐标
            oy: source.startY * scale.hScale, //上一次的y坐标
            ow: source.width * scale.wScale, //上一次的宽度
            oh: source.height * scale.hScale, //上一次的高度
            x: source.startX * scale.wScale, //x坐标
            y: source.startY * scale.hScale, //y坐标
            w: source.width * scale.wScale, //宽度
            h: source.height * scale.hScale, //高度
            show: false, //是否显示
            name: this.getInputSourceStatusById(source.inputSourceId).name, //源名称
            isAlignRight: false, //是否开启右侧对齐(左下角按钮操作时需开启右侧对齐)
            isLock: !!source.isLocked, //锁定源后不能移动和层级操作
            id: source.inputSourceId,//源Id
            type: source.inputSourceType,//类型
            zIndex: source.priority, //Z序
            winId: source.id,
            status: this.getInputSourceStatusById(source.inputSourceId).status,
            replaceStatus: false,
            count: 0,
            sourceType: 'normal'
          });
          this.openedSources.push(tempSource);
        });
      },
      sortOpenedSource() {
        let osdList = this.openedSources.filter(source => {
          return source.sourceType === 'osd';
        });
        if(osdList.length > 0) {
          if(osdList[0].zIndex === 0) {
            osdList[0].zIndex = 1;
            this.openedSources.filter(source => {
              return source.sourceType !== 'osd';
            }).forEach((element, i) => {
              element.zIndex = i + 2;
            });
          } else {
            osdList[0].zIndex = 999;
          }
        }
      }
    }
  };
</script>



